const jobs = [{"entity": "OpenDP", "title": "Open-Source Community Manager", "url": "https://sjobs.brassring.com/TGnewUI/Search/Home/Home?partnerid=25240&siteid=5341#jobDetails=1950880_5341", "percentTime": 100, "percentOSS": 100, "deadline": "2022-12-31", "expires": "2022-12-31", "location": "On-Site and Remote Hybrid", "description": "OpenDP is an open-source initiative for building libraries, tools, and systems to share and analyze sensitive data with the strong mathematical protections of differential privacy.  It has a rapidly growing community that spans academia, industry, and government. The OpenDP community manager will assist the faculty directors, the executive committee, and the software development team in community outreach, engagement, governance, diversity, and inclusion.\n\nThis is a one year term appointment with the possibility of renewal.\n\n1. Plan and execute strategies to grow a diverse, inclusive open-source community, measuring, improving and reporting on metrics about diversity, equity and inclusion in the OpenDP community.\n2. Engage with stakeholders within and outside the OpenDP community; manage communication and outreach on a regular basis over a variety of channels, such as website, email, mailing list, Github repos, GitHub Discussions, Slack, Twitter, etc.\n3. Implement and manage the onboarding process for new community members and contributors to the OpenDP project.\n4. Assist with developing and communicating the processes for contribution and vetting.\n5. Organize and manage OpenDP GitHub repos and issues; review and update existing GitHub issues for clarity and contributor-friendliness.\n6. Review, prioritize, and track user requests and feedback via a variety of channels as mentioned above; assist with outreach to potential new users of the OpenDP software.\n7. Organize and manage community working groups addressing specific goals and tasks\n8. Organize virtual and in-person events for the community, such as community meetings, workshops and webinars.\n9. Assist with roadmap planning by advocating for community needs.\n10. Guide OpenDP developers, stakeholders, and community members in documenting user stories, technical specifications, and contributor and user guides.\n11. Curate resources for community learning and training, in both differential privacy and the specifics of OpenDP.\n\n### Basic Qualifications\n\n* Minimum of two years' post-secondary education or relevant work experience\n\n### Additional Qualifications and Skills\n\n* Exposure to and interest in learning more about differential privacy.\n* Experience and interest in working with open-source projects and communities.\n* Excellent written and verbal communication skills.\n* Excellent interpersonal skills and empathy, valuing diversity and working with communities. \n\n### Additional Information\n\nSEAS is not able to provide visa sponsorship for this position.\n\nThis is a one year term appointment, with the possibility of renewal.\n\n* The University requires all Harvard community members to be fully vaccinated against COVID-19 and remain up to date with COVID-19 vaccine boosters, as detailed in Harvard's Vaccine & Booster Requirements. Individuals may claim exemption from the vaccine requirement for medical or religious reasons. More information regarding the University's COVID vaccination requirement, exemptions, and verification of vaccination status may be found at the University's \"COVID-19 Vaccine Information\" webpage: http://www.harvard.edu/coronavirus/covid-19-vaccine-information/. \n* This is an in-person position based on our campus in Allston, MA.  Due to the COVID-19 pandemic, it is possible that the work may begin with a hybrid model.  All remote work must be performed in a state in which Harvard is registered to do business (CA, CT, MA, MD, ME, NH, NY, RI, and VT). The remote nature of this job is only temporary.  In-person on-campus work will be required as a condition of continued employment once the University and your supervisor determine that it is appropriate. Individual flexible and remote work options for this role can be discussed during the interview process. \n\n\n### About SEAS\nThe Harvard John A. Paulson School of Engineering and Applied Sciences serves as the connector and integrator of Harvard's teaching and research efforts in engineering, applied sciences, and technology. Our core tenets are educating broad-minded students; interdisciplinary research; integration across disciplines; and balancing theory, experimentation and practice to create an unmatched environment for learning and exploration. Through collaboration with researchers from all parts of Harvard, other universities, and industry, we bring discovery and innovation directly to bear on improving human life and society. For more information about us, please visit www.seas.harvard.edu.\n\n### Commitment to Equity, Diversity, Inclusion, and Belonging\nHarvard University views equity, diversity, inclusion, and belonging as the pathway to achieving inclusive excellence and fostering a campus culture where everyone can thrive. We strive to create a community that draws upon the widest possible pool of talent to unify excellence and diversity while fully embracing individuals from varied backgrounds, cultures, races, identities, life experiences, perspectives, beliefs, and values.\n\n### EEO Statement\n\nWe are an equal opportunity employer and all qualified applicants will receive consideration for employment without regard to race, color, religion, sex, national origin, disability status, protected veteran status, gender identity, sexual orientation, pregnancy and pregnancy-related conditions, or any other characteristic protected by law.\n", "date": "2022-09-02", "id": "2022-09-02_opendp"}, {"entity": "Unitary Fund", "title": "Chief of Staff", "url": "https://unitary.fund/jobs/chief_of_staff.html", "percentTime": 100, "percentOSS": 80, "deadline": "2022-07-26", "expires": "2022-09-26", "location": "Remote", "description": "This opening is a rare opportunity to help run a new type of research\nnon-profit and contribute to the developing quantum technology ecosystem.\n\nUnitary Fund is a 501(c)(3) non-profit working to create a quantum\ntechnology ecosystem that benefits the most people.\nWe run a grant program to support new open source quantum tech,\nand we fund our own research on key projects for the ecosystem.\n\n\nWe\u2019re looking for a proactive generalist who can contribute and learn\nacross the organization. They will work directly with the President and\nthe CTO to manage and scale processes and projects.\n\nExamples can include scaling our microgrant process, raising funds from\nnew supporters, managing our bounty programs and hackathons,\nbringing on new partner organizations and new geographies,\ncontracting a new website build, and other special projects.\n\nThis role encompasses work across multiple aspects of the organization\n(microgrants and Unitary Labs) and will require a strongly organized\nindividual to surface priorities, triage needs, communicate continuously,\nnd know when to ask for help. This position will work closely with\nUnitary Fund leadership and have opportunities to develop their own\nleadership as well. We are looking for someone excited to take ownership\nof the projects that they work on.\n", "date": "2022-07-26", "id": "2022-07-26_unitaryfund"}, {"entity": "Spyder", "title": "Spyder Junior Developer", "url": "https://tinyurl.com/spyder-grant-job", "percentTime": 100, "percentOSS": 100, "deadline": "2022-05-22", "expires": "2022-05-23", "location": "Remote", "description": "[Spyder](https://www.spyder-ide.org/) is a free and open source scientific \nenvironment written in Python, for Python, and designed by and for \nscientists, engineers and data analysts. It features a unique combination \nof the advanced editing, analysis, debugging, and profiling functionality \nof a comprehensive development tool with the data exploration, interactive \nexecution, deep inspection, and beautiful visualization capabilities of a \nscientific package.\n\nWe are looking for someone to help us improve the installation experience \nfor the Spyder IDE Windows users, by enabling the current standalone \ninstallers available to be auto-updatable and providing a way to install \ncustom packages that are not bundled with the installers in a simpler way.\n\nThe full job/project description and application process is available on \n[this document](https://docs.google.com/document/d/e/2PACX-1vTUQDK4zrmwJ0E_KQehqmAjWWBFM8aJSlxCOY4m8k64pYnhYn81OMg6VaCPKXdx3DHjbSbxWjOlUMEs/pub)\n", "date": "2022-04-29", "id": "2022-04-29_spyder"}, {"entity": "ArviZ", "title": "Dataviz External Consultant", "url": "https://github.com/arviz-devs/GSoD/blob/main/2021/viz_evaluation.md", "percentTime": "NA", "percentOSS": 100, "deadline": "2022-07-31", "expires": "2022-07-31", "location": "Remote", "description": "Dataviz is a key part of any statistical or data science job, and Bayesian\nmodels are not an exception. ArviZ provides tools for Exploratory Analysis\nof Bayesian Models, from diagnostics and summary statistics to bespoke\nvisualizations.\n\nWe are searching for someone to analyze the current state of available\nplots to identify missing visualizations, defaults not following bad\npractices and missing references in the documentation and write a\nreport with the findings and recommendations. The report will be published\nin hopes it also helps other dataviz libraries.\n\nThe full job description and application process is available on\n[our GitHub](https://github.com/arviz-devs/GSoD/blob/main/2021/viz_evaluation.md)\n", "date": "2022-03-12", "id": "2022-03-12_arviz"}, {"entity": "NVIDIA", "title": "PyData Open Source Developer", "url": "https://tinyurl.com/nvidia-pydata-job", "percentTime": 100, "percentOSS": 80, "deadline": "2022-09-30", "expires": "2022-09-30", "location": "Remote", "description": "Hundreds of thousands of AI engineers, data scientists, and data engineers rely \non both NVIDIA technologies and related PyData open source projects to do their \nlife's work. We are continuing to expand our support of the PyData ecosystem and \nlooking to hire strong engineers who are or can become contributors to libraries \nlike Pandas, Scikit-learn, NumPy, and more! This is an excellent opportunity for \nyou to balance between contributing to and helping users of these \nopen source projects.\n\nA full job description and application form can be found on the \n[NVIDIA career site job posting](https://nvidia.wd5.myworkdayjobs.com/NVIDIAExternalCareerSite/job/US-CA-Remote/Senior-Software-Engineer--PyData-Open-Source-Developer---RAPIDS_JR1954810).", "date": "2022-03-01", "id": "2022-03-01_nvidia"}, {"entity": "TU Delft & SymPy", "title": "Postdoctoral Researcher Opening: Advancing Biomechanical Modeling By Improving SymPy Code Generation (x/f/m)", "url": "https://tinyurl.com/sympy-job-2022", "percentTime": 100, "percentOSS": 100, "deadline": "2022-02-28", "expires": "2022-03-01", "location": "Delft, Netherlands with possibility of remote", "description": "The TU Delft Bicycle Lab is seeking a postdoctoral researcher for a 1\nyear equivalent contract (full or part time). You will work on a\nsub-project of [SymPy\\'s \\\"Improving Foundational Open Source Symbolic\nMathematics for Science\\\"\ngrant](https://chanzuckerberg.com/eoss/proposals/sympy-improving-foundational-open-source-symbolic-mathematics-for-science/)\nfunded by the [Chan-Zuckerberg Foundation's \"Essential Open Source\nSoftware for Science\\\"\nprogram](https://chanzuckerberg.com/newsroom/czi-awards-16-million-for-foundational-open-source-software-tools-essential-to-biomedicine/).\nThe purpose of the overall grant is to improve the SymPy Python symbolic\nmathematics library in the key areas of performance, code generation,\nand documentation. The [full grant\nproposal](https://doi.org/10.6084/m9.figshare.16590053.v1) is available\npublicly on Figshare.\n\n**Applications Due February 28, 2022**\n\nMore information about the job can be found at\nhttps://mechmotum.github.io/blog/sympy-czi-postdoc.html\n", "date": "2022-02-04", "id": "2022-02-04_sympy"}, {"entity": "rOpenSci", "title": "Community Manager", "url": "https://ropensci.org/careers/community-manager-2022", "percentTime": 100, "percentOSS": 100, "deadline": "2022-03-01", "expires": "2022-03-02", "location": "Remote", "description": "rOpenSci seeks a _Community Manager_ to support and build our vibrant and inclusive community\nof researchers and software engineers and help expand the scope and impact of our work in\nopen science.\n\nrOpenSci is a not-for-profit organization with the mission to expand access to scientific data\nand promote a culture of reproducible research and sustainable research software.\nrOpenSci staff and community are fully remote, with core team members in North America and\nEurope and a global community of contributors.  We're looking for a new community manager\nto help us to consolidate our existing efforts and ensure that we're supporting an increasingly\ndiverse membership.\n\nThis is a long-term, full-time contract position with a salary range of $70,000-$85,000,\ncommensurate with experience, skills, and local benefits requirements.  The position is 100%\nremote. Costs related to a home office or co-working space will be supported,\nas well as professional development opportunities,\n\nA full job description and application form can be found at <https://ropensci.org/careers/community-manager-2022/>.", "date": "2022-02-03", "id": "2022-02-03_ropensci"}, {"entity": "Unitary Fund", "title": "Member of the Technical Staff (part-time)", "url": "https://unitary.fund/jobs/mts.html", "percentTime": 40, "percentOSS": 100, "deadline": "2022-02-13", "expires": "2022-02-13", "location": "Remote", "description": "We\u2019re hiring Members of Technical Staff with a focus on Mitiq.\nYou'll be part of the core development team of [Mitiq](https://github.com/unitaryfund/mitiq), the open-source quantum\nerror mitigation toolkit, in collaboration with key players from the industry and research sector.\n\nUnitary Fund is a 501(c)(3) non-profit working to create a quantum technology ecosystem that benefits the most people.\nWe run a grant program to support new open source quantum tech, and we fund our own research on key projects for the ecosystem.\nWe\u2019re hiring for a part-time marketing and communications role on our team.\nThis opening is a rare opportunity to contribute to the developing quantum technology ecosystem.\nThe full job description can be found [here](https://unitary.fund/jobs/mts.html)", "date": "2022-01-13", "id": "2022-01-13_unitaryfund"}, {"entity": "GNU Radio", "title": "Technical writer", "url": "https://www.gnuradio.org/news/2021-11-30-numfocus-jobs/", "percentOSS": 100, "deadline": "2022-04-03", "expires": "2022-04-03", "location": "Remote", "description": "GNU Radio has recently won a small grant from NumFOCUS to help improve GNU Radio\u2019s tutorials, as they play a major role in helping new users get through the steep learning curve. We are looking to hire a part-time contractor to perform the work of one GNU Radio expert. It is anticipated that this work will be performed during Q1 of 2022.\n\n### Rate\n\n$800 total, $40 per hour, 20 hours total\n\n### Task\n\n* Starting at the top of the tutorial list and working your way down (or using an order of your choosing), go through as many tutorials as the funding/time allows, and do a single editing pass through each one, to improve clarity and writing quality. Readability is more important than using \u201cproper technical style\u201d; many readers aren\u2019t going to have English as their first language. Any improvements to visual presentation, such as wiki formatting to make the content look better (e.g., shrink an image that is clearly way too large compared to the surrounding content), is also desired.\n\nEmail jobs@gnuradio.org if you are interested in either of these positions. Thanks!\n", "date": "2021-12-03", "id": "2021-12-03_gnuradio"}, {"entity": "GNU Radio", "title": "GNU Radio Expert", "url": "https://www.gnuradio.org/news/2021-11-30-numfocus-jobs/", "percentOSS": 100, "deadline": "2022-04-01", "expires": "2022-04-01", "location": "Remote", "description": "GNU Radio has recently won a small grant from NumFOCUS to help improve GNU Radio\u2019s tutorials, as they play a major role in helping new users get through the steep learning curve. We are looking to hire a part-time contractor to perform the work of one GNU Radio expert. It is anticipated that this work will be performed during Q1 of 2022.\n\n### Rate\n\n$3600 total, $60 per hour, 60 hours total spread across at least 1 month (max 3 months)\n\n### Tasks (tackle one or more of the following tasks)\n\n* Complete overhaul of the QPSK and BPSK tutorials reflecting the change from \u2018Polyphase Clock Sync\u2019 to \u2018Symbol Sync\u2019, and from \u2018CMA Equalizer\u2019 to \u2018Linear Equalizer\u2019 and \u2018Adaptive Algorithm\u2019.\n* The Embedded Python Block tutorial should be the first tutorial that dives into how to write a block (in Python) but it\u2019s currently performing double duty as the Embedded Python Block \u201cblock docs\u201d page, so the task is to separate them into two pages and improve the tutorial itself.\n* Pick one or more of our existing tutorials and propose what you will do to improve it, then get approval from the GNU Radio leadership, and then execute on the proposed improvements. The step prior to approval cannot involve billing more than 4 hours.\n* If the contractor has any specific expertise, e.g. in an application of GNU Radio we don\u2019t currently have a tutorial about, the contractor can create a new tutorial after getting GNU Radio leadership approval.\n\nEffort should be spread relatively evenly across at least 1 month (with a max of 3 months), so that the GNU Radio Expert has time to share midterm progress with the GNU Radio leadership and obtain feedback. To enforce this, we will limit the amount billed each week to no more than 16 hours. The effort must be completed within 3 months after the start of work.\n\nEmail jobs@gnuradio.org if you are interested in either of these positions. Thanks!\n", "date": "2021-11-30", "id": "2021-11-30_gnuradio"}, {"entity": "Zarr", "title": "Community Manager", "percentTime": 100, "percentOSS": 100, "deadline": "2021-12-15", "expires": "2021-12-21", "location": "Remote", "description": "Zarr is a format for the storage of chunked, compressed, N-dimensional arrays.\nBuilt originally in Python for working with NumPy arrays, Zarr is now supported\nin more than half a dozen languages. With funding from the Chan Zuckerberg\nInitiative, we are looking to hire a full-time, open-source enthusiast for two\nyears to work as our community manager.\n\nAbout the Role:\n\nAs the first full-time position dedicated to Zarr, you will have the\nopportunity to shape the role to meet the needs of the community. You will be\nan advocate for our strong, inclusive community, as well as define ways to\nencourage new contributions while balancing the needs of a maturing data\nformat.\n\nWe are looking for someone who can:\n\n* Foster and build relationships online\n* Communicate clearly and technically with open-source developers\n* Be a conduit to the research user communities\n* Structure the project for growth and resiliency\n\nThis is a fully remote position. Responsibilities:\n\n* Work with open source contributors to review, refine, and merge contributions\n* Identify and mentor potential new core-developers\n* Design a specification process that fosters quick decisions on emerging user cases\n* Report developments via social media, the Zarr website and scientific publications\n* Plan, organize, and run regular community calls\n* Attend online and in-person events to speak about the project\n* Help to build a welcoming, inclusive, and supportive community environment!\n\n## Experience:\n\nCandidates should have experience in one or more programming languages with a\nstrong tendency towards Python and be comfortable working with Git and GitHub.\n\n## Compensation:\n\nThis role can be either a contract position or an employed position with fringe\nbenefits. $60,000 \u2013 $80,000 per year dependent on position type and experience.\nIf you are looking for a flexible, open-source position that can make a world\nof difference in bioimaging, climate science, or more. Please get in touch via\nhiring@numfocus.org.\n", "date": "2021-11-18", "id": "2021-11-18_zarr"}, {"entity": "Quansight", "title": "Sr. Frontend Engineer", "url": "https://quansight.breezy.hr/p/47a9c3e35069-senior-frontend-software-engineer", "percentTime": 100, "percentOSS": 60, "badges": ["Sponsor"], "deadline": "2021-10-13", "expires": "2021-10-13", "location": "Remote", "description": "We are looking for a Senior Frontend Engineer to join Quansight Labs and our \nmission of supporting the PyData open-source ecosystem. \nAs a Senior Frontend Engineer, you will lead our internal web-related projects \nand contribute to the community-driven open-source projects supported by \nQuansight Labs. We are seeking great engineers who are passionate about web \ndevelopment, open source, accessibility and who possess a collaborative and \ncommunity-first mindset.\n", "date": "2021-10-13", "id": "2021-10-13_quansight"}, {"entity": "Unitary Fund", "title": "Communications and Marketing (part-time)", "url": "https://unitary.fund/jobs/marketing_comm.html", "percentTime": 25, "percentOSS": 100, "deadline": "2021-11-19", "expires": "2021-11-19", "location": "Remote", "description": "Unitary Fund is a 501(c)(3) non-profit working to create a quantum technology ecosystem that benefits the most people. \nWe run a grant program to support new open source quantum tech, and we fund our own research on key projects for the ecosystem. \nWe\u2019re hiring for a part-time marketing and communications role on our team. \nThis opening is a rare opportunity to contribute to the developing quantum technology ecosystem.\nThe full job description can be found [here](https://unitary.fund/jobs/marketing_comm.html)\n", "date": "2021-10-12", "id": "2021-10-12_unitaryfund"}, {"entity": "Quansight", "title": "Contributor Experience Lead", "url": "https://quansight.breezy.hr/p/361a2e7da19b-contributor-experience-lead", "percentTime": 100, "percentOSS": 100, "badges": ["Sponsor"], "deadline": "2021-10-31", "expires": "2021-10-31", "location": "Remote", "description": "We are looking for a Contributor Experience Lead, who will work across\nprojects to identify, document, and implement practices to foster inclusive\nopen-source communities for NumPy, SciPy, Matplotlib, and Pandas.\n", "date": "2021-09-09", "id": "2021-09-09_quansight"}, {"entity": "Kitware", "title": "Medical Image Analysis Researcher", "url": "https://www.kitware.com/careers", "percentTime": 100, "percentOSS": 75, "deadline": "2021-10-30", "expires": "2021-10-30", "location": "Carrboro, NC", "description": "Team Description: \nKitware's Medical Computing team solves complex healthcare challenges through customized software solutions. The team provides research, development, and technology integration services to research centers, universities and companies in the medical and biomedical sectors. By joining our team you will participate in a dynamic multidisciplinary work environment with exceptionally talented computer scientists, software engineers, mechanical engineers and biomedical engineers.| \n\nAbout the projects: \nWe build powerful software solutions using our well-established open source platforms including VTK, ITK, 3D Slicer, Pulse, iMSTK, CMake and ParaView. These platforms are mature, supported, documented, and thoroughly tested software systems that are in use by tens of thousands of developers and end-users worldwide. Our commercial projects involve generating solutions for medical device companies from ultrasound and preclinical imaging companies, to surgery device companies. Our research projects involve collaborations with top research institutions, including Harvard Medical School, MIT, Cleveland Clinic, Duke University, and others and build on our open source platforms.\n", "date": "2021-04-30", "id": "2021-04-30_kitware"}, {"entity": "Aperio Software", "title": "Software Developer", "url": "https://aperiosoftware.com", "percentTime": 100, "percentOSS": 80, "deadline": "2021-05-14", "expires": "2021-05-30", "location": "Remote", "description": "Aperio Software is a company that specialises in developing software and providing software training in scientific domains for a variety of organisations and open source projects.\nPast and current projects include the development of user tools and calibration software for the Daniel K. Inouye Solar Telescope (DKIST), work on the AAS WorldWide Telescope project, contributions to the Astropy and SunPy projects, and development of the glue visualisation tool.\n\nAperio is seeking a software developer and experienced open-source contributor to work with us on our growing number of projects in solar physics and astronomy, primarily in established open-source projects.\nThis will include working on the development, testing, documentation and maintenance of several different software packages.\nYou will be required to liaise with clients, other staff and the open-source community. \nIn addition, there will be the opportunity to get involved in designing and delivering programming workshops.\nFor more details see the full job description at [aperiosoftware.com/jobs](https://aperiosoftware.com/jobs).\n", "date": "2021-04-30", "id": "2021-04-30_aperio"}, {"entity": "The Julia Language", "title": "Technical Writer", "url": "https://discourse.julialang.org/t/the-julia-language-is-hiring-a-technical-writer-as-part-of-google-season-of-docs-2021/59806", "percentTime": 25, "percentOSS": 100, "deadline": "2021-05-10", "expires": "2021-05-10", "location": "Remote", "description": "The Julia Language is excited to share that we will be hiring a technical writer as part of Google Season of Docs 2021. For more details, check out the [full job post](https://discourse.julialang.org/t/the-julia-language-is-hiring-a-technical-writer-as-part-of-google-season-of-docs-2021/59806).\n", "date": "2021-04-22", "id": "2021-04-22_julia"}, {"entity": "Coiled", "title": "OSS Engineer", "url": "https://coiled.io/careers/", "percentTime": 100, "percentOSS": 80, "badges": ["Sponsor"], "deadline": "2021-10-14", "expires": "2021-10-14", "location": "Remote", "description": "This role is for a software engineer to work on the open source Dask project.\nYou will be able to build and maintain Dask and its connections with surrounding\nprojects.\n\nThis role will also ensure that Coiled customers are effective in their use of\nDask to scale Python. You will interact with users (both community and clients),\nengage in team prioritization, and work to implement fixes and features to improve\nthe experience and capabilities of the Python ecosystem, verifying that those fixes\nresolve user needs.\n", "date": "2021-04-14", "id": "2021-04-14_coiled"}, {"entity": "Quansight", "title": "PyData maintainer", "url": "https://www.quansight.com/careers", "percentTime": 100, "percentOSS": 60, "badges": ["Sponsor"], "deadline": "2021-10-13", "expires": "2021-10-13", "location": "Remote", "description": "This is an open hiring call for candidates who are already heavily invested\nin contributing to the PyData open source ecosystem and are looking for a\ncompany that can support their passion. The specifics of your role will be\ndetermined during the interview process.\n", "date": "2021-04-13", "id": "2021-04-13_quansight"}]
export default jobs;
